.page-login {
  min-height: 100dvh;
  margin: auto;
}
.page-login .flex-wrap {
  display: flex;
  flex-wrap: wrap;
  min-height: 100dvh;
  align-content: start;
}
.page-login .topbar,
.page-login .main-left {
  display: none;
}
.page-login__left, .page-login__right {
  width: 50%;
  min-height: 100dvh;
}
.page-login__left {
  background: var(--colormain) url(images/graphics/img-login.png);
  padding: 40px;
  color: #fff;
  background-size: cover;
  background-position: bottom center;
}
.page-login__left .logo {
  font-size: 18px;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 30px;
}
.page-login__left .logo:hover {
  color: #fff;
}
.page-login__left .title {
  font-weight: 500;
  font-size: 32px;
  line-height: 1.4;
}
.page-login__right {
  padding: 5% 7.8%;
  position: relative;
}
.page-login__right .close-menu {
  position: absolute;
  top: 20px;
  right: 20px;
}
.page-login .form-login {
  margin: 0 auto;
  font-size: 16px;
  font-weight: 300;
}
.page-login .form-login .error {
  color: red;
  font-size: 14px;
}
.page-login .form-login .btn-default.bg-before {
  color: #568775;
}
.page-login .form-login .btn-default.active {
  color: #fff;
}
.page-login .form-login .form-control {
  border: 0;
  border-radius: 0;
  border-bottom: 1px solid #abb0ae;
  background: none;
  font-weight: 300;
  padding: 10px;
  color: var(--colormain);
}
.page-login .form-login .relative .icon-svg {
  left: 0;
  top: 10px;
}
.page-login .form-login .relative .form-control {
  padding-left: 35px;
}
.page-login .form-login .grid-otp {
  column-gap: 16px;
  row-gap: 16px;
}
.page-login .form-login .grid-otp .form-control {
  background: #fff;
  border-radius: 4px;
  border: 1px solid #DBE2DF;
  text-align: center;
}
.page-login .form-login .send-back {
  display: flex;
  align-items: center;
  gap: 3px;
  justify-content: center;
  color: #656867;
  margin-top: 200px;
}
.page-login .login-social a {
  border-radius: 8px;
  border: 1px solid #DBE2DF;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  gap: 10px;
  margin-bottom: 30px;
}
.page-login .login-social a img {
  width: 47px;
}
.page-login .step-reset-pass {
  display: flex;
  justify-content: center;
}
.page-login .step-reset-pass .step {
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  color: #656867;
  width: 50%;
  position: relative;
}
.page-login .step-reset-pass .step::after {
  content: "";
  background: #dbe2df;
  position: absolute;
  height: 2px;
  width: 100%;
  top: 18px;
  left: 50%;
}
.page-login .step-reset-pass .step .number {
  background: var(--bg-main);
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #DBE2DF;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin: 0 auto 10px;
}
.page-login .step-reset-pass .step .number::before {
  content: "";
  border: 5px solid var(--bg-main);
  width: 46px;
  height: 46px;
  position: absolute;
}
.page-login .step-reset-pass .step:last-child::after {
  display: none;
}
.page-login .step-reset-pass .step.active {
  color: var(--colormain);
}
.page-login .step-reset-pass .step.active .number {
  border: 2px solid var(--colormain);
}
.page-login .step-reset-pass .step.succsess::after {
  background: var(--colormain);
}
@media (max-width: 1025px) {
  .page-login__left {
    padding: 30px;
  }
  .page-login__left .logo {
    margin-bottom: 20px;
  }
  .page-login__left .title {
    font-size: 24px;
  }
  .page-login__right {
    padding: 40px;
  }
  .page-login__right .title-cate {
    font-size: 24px;
  }
}
@media (max-width: 991px) {
  .page-login__left {
    padding: 20px;
  }
  .page-login__left .title {
    font-size: 20px;
  }
  .page-login__right {
    padding: 20px;
  }
  .page-login__right .title-cate {
    font-size: 20px;
  }
  .page-login .form-login {
    font-size: 15px;
  }
  .page-login .form-login .login-social a {
    height: 45px;
    gap: 10px;
    margin-bottom: 20px;
  }
  .page-login .form-login .login-social a img {
    width: 40px;
  }
}
@media (max-width: 600px) {
  .page-login {
    display: block;
  }
  .page-login .main-left {
    display: block;
  }
  .page-login .mb30 {
    margin-bottom: 30px !important;
  }
  .page-login__left {
    width: 100%;
    background: none;
    min-height: auto;
    padding: 0;
  }
  .page-login__left .topbar {
    display: flex;
  }
  .page-login__left .img-login {
    display: none;
  }
  .page-login__left .logo {
    margin-bottom: 0;
  }
  .page-login__right {
    padding: 40px 20px;
    width: 100%;
    position: static;
  }
  .page-login__right .close-menu {
    display: none;
  }
  .page-login .form-login .grid-otp {
    grid-template-columns: repeat(5, 1fr);
  }
  .page-login .other-requirements {
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
  }
}